@import url('https://fonts.googleapis.com/css2?family=Ubuntu:wght@400;500;700&display=swap');
*{
   font-family: 'Ubuntu';
   color: #263238;
   margin: 0;
   padding: 0;
}

/* Headings CSS */

.skills_heading, .project_heading{
    font-size: 60px;
    text-align: center;
    text-decoration: underline;
}

/* Navbar CSS */


.header_navbar{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-evenly;
    padding-left: 30px;
    padding-right: 30px;
    padding-top: 20px;
}

.navbar{
    display: flex;
}
.options{
    display: flex;
    align-items: center;
}
.logo img{
    width: 200px;
}
ul li{
    list-style: none;
    margin-left: 40px;
    font-weight: 700;
    font-size: 20px;
    cursor: pointer;
}
ul .home{
    color: #ff8c92;
}
ul li:hover {
    color: #ff8c92;
    transition: .3s;
}
.options button {
    background-color: #263238;
    color: white;
    margin-left: 40px;
    font-weight: 700;
    font-size: 20px;
    padding: 30px 50px 30px 50px;
    border-radius: 50px;
    cursor: pointer;
    border: none;
}
.options button:hover{
    background-color: #ff8c92;
    color: white;
    transition: .3s;
}

/* Header Content CSS */

.header_content{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    padding: 50px 20px 50px 20px;
}
.header_content img{
    width: 600px;
    padding-top: 20px;
}

.left_section .hw{
    font-size: 30px;
    line-height: 30px;
    left: 10px;
}
.left_section .vs, .fd{
    font-size: 70px;
    margin-bottom: 10px;
}
.left_section .fd{
    font-weight: lighter;
    color: #ff8c92;
}
.left_section p{
    font-size: 20px;
    width: 500px;
    line-height: 30px;
}
.left_section .hire_me{
    background-color: #263238;
    color: white;
    margin-right: 40px;
    font-weight: 700;
    font-size: 20px;
    padding: 30px 50px 30px 50px;
    border-radius: 50px;
    cursor: pointer;
    border: none;
    margin-top: 40px;
}
.left_section .hire_me:hover{
    background-color: #ff8c92;
    color: white;
    transition: .3s;
}
.left_section .resume{
    background-color: #263238;
    color: white;
    margin-right: 40px;
    font-weight: 700;
    font-size: 20px;
    padding: 30px 50px 30px 50px;
    border-radius: 50px;
    cursor: pointer;
    border: none;
    margin-top: 40px;
}
.left_section .resume:hover{
    background-color: #ff8c92;
    color: white;
    transition: .3s;
}

/* Skill CSS */

.display_skills{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    align-items: center;
    background-color: #f5f4f5;
    margin: 30px;
    padding: 30px;
    border-radius: 30px;
}
.skill_content{
    background-color: white;
    width: fit-content;
    text-align: center;
    width: 150px;
    padding: 20px;
    border-radius: 30px;
    margin: 20px;
    border: 2px solid white;
    
}
.skill_content img{
    width: 100px;
    padding-top: 20px;
}
.skill_content h1{
    color: #263238;
}

/* Project CSS */

.project_container{
    background-color: #f5f4f5;
    padding: 30px;
    margin: 30px;
    border-radius: 30px;
}
.project_content{
    display: flex;
    justify-content: space-evenly;
    background-color: #dd8c9a;
    padding: 30px;
    margin: 40px 60px 40px 60px;
    border-radius: 30px;
    height: fit-content;
}
.project_left .project_button{
    background-color:#263238;
    color: white;
    margin-right: 40px;
    font-weight: 700;
    font-size: 20px;
    padding: 30px 50px 30px 50px;
    border-radius: 50px;
    cursor: pointer;
    border: none;
    margin-top: 30px;
}
.project_left .project_button:hover{
    background-color: #ff8c92;
    color: white;
    transition: .3s;
}
.project_title{
    margin-bottom: 20px;
    margin-top: 20px;
}
.project_left .project_details{
    width: 400px;
    line-height: 25px;
}

.project_right img{
    position: relative;
    width: 400px;
    border-radius: 30px;
    align-items: center;
}
 
/* Hire Me Form */

input{
    border: none;
    border-bottom: 2px solid #263238;
    font-size: 30px;
    height: 60px;
    margin: 40px;
    padding: 20px;
}

.submit{
    background-color: #263238;
    color: white;
    margin-left: 40px;
    font-weight: 700;
    font-size: 20px;
    padding: 30px 50px 30px 50px;
    border-radius: 50px;
    cursor: pointer;
    border: none;
    margin-top: 40px;
}
.submit:hover{
    background-color: #ff8c92;
    color: white;
    transition: .3s;
}


/* Footer */

.footer_container{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    top: 0;
    margin-bottom: 30px;
    margin-top: 40px;
    padding: 20px;
}
.footer_logo img{
    width: 200px;
    margin-top: 20px;
}
.social a img{
    width: 30px;
    margin-right: 10px;
}




@media screen and (max-width: 1200px){
    .project_container{
        background-color: #f5f4f5;
        padding: 30px;
        margin: 30px;
        border-radius: 30px;
        min-width: fit-content;
    }
    .project_content{
        display: flex;
        justify-content: space-evenly;
        background-color: #dd8c9a;
        padding: 30px;
        width: fit-content;
        border-radius: 30px;
        position: relative;
    }
    .project_left .project_details{
        width: auto;
        font-size: 20px;
        line-height: 25px;
        padding: 20px;
        position: relative;
    }
    .display_skills{
        display: flex;
        text-align: center;
        width: fit-content;
        justify-content: center;
        margin: 0px;
        min-width: fit-content;
    }
    .header_navbar{
        display: flex;
        justify-content: flex-start;
    }
    
    .footer_container{
    justify-content: space-around;
    top: 0;
    margin-bottom: 30px;
    margin-top: 40px;
    padding: 20px;
    }
}

@media screen and (max-width: 1045px){
    .project_content{
        display: flex;
        justify-content: space-evenly;
        background-color: #dd8c9a;
        padding: 30px;
        width: fit-content;
        border-radius: 30px;
    }
    .project_right img{
        display: none;
    }
    .project_left .project_details{
        width: auto;
        font-size: 20px;
        line-height: 25px;
        padding: 20px;
    }
    .header_content{
        display: flex;
        flex-wrap: wrap;
        text-align: center;
        width: fit-content;
        justify-content: space-around;
        align-items: center;
        padding: 50px 20px 50px 20px;
    }
    .display_skills{
        display: flex;
        text-align: center;
        width: fit-content;
        justify-content: center;
        
    }
    .navbar{
        display: flex;
        width: min-content;
    }
    .display_skills{
        display: flex;
        justify-content: space-around;
        flex-wrap: wrap;
        align-items: center;
        background-color: #f5f4f5;
        margin: 30px;
        padding: 30px;
        border-radius: 30px;
        width: 500px;
    }
    .project_container{
        background-color: #f5f4f5;
        padding: 30px;
        margin: 30px;
        border-radius: 30px;
        width: 500px;
    }
    .right{
        margin: 60px;
    }
    .left_footer{
        margin: 60px;
    }

    /* Navbar */
    .options{
        display: none;
    }
    .header_navbar{
        display: flex;
        justify-content: flex-start;
        flex-wrap: nowrap;
    }
    
.footer_container{
   display: block;
}
.footer_logo img{
    width: 200px;
    margin-left: 50px;
}
}

